@use './colors';

.drop-down-parent {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  gap: 8px;
  color: black;
  position: relative;
  .drop-down-container {
    font-size: 18px;
    background: #ffffff;
    padding: 5px 0px 5px 0px;
    gap: 1px;
    border: solid 1px #89969c;
    border-radius: 14px;
    box-shadow: 0px 4px 14px 0px #0000001a;
    display: none;
    position: absolute;

    .search-input {
      .checkbox-container {
        display: flex;
        flex-direction: column;
        gap: 10px;
        padding: 10px 5px;
        width: 230px;
        height: 220px;
        overflow-y: auto;
        &::-webkit-scrollbar {
          width: 4px;
        }
        &::-webkit-scrollbar-thumb {
          background-color: colors.$background_green;
          border-radius: 26px;
          cursor: pointer;
        }
      }
      .search-container {
        display: flex;
        align-items: center;
        gap: 5px;
        padding: 0px 2px;
        border-bottom: 1px solid #ccc;
        input {
          border: 0px;
          outline: none;
          padding: 10px 0px;
        }
      }
      .lupa {
        position: relative;
        left: 0px;
        width: 20px;
      }
      .clear {
        position: absolute;
        right: 10px;
      }
      .checkbox-parent {
        display: flex;
        gap: 7px;
        input {
          border: none;
          padding: 6px 0px;
          font-size: 14px;
          &:focus {
            border: none;
            outline: none;
          }
        }
      }
    }
    .checkbox-remote {
      width: 200px;
      height: 190px;
    }

    .mdl {
      margin-top: 30px;
    }
  }

  .drop-down {
    background: none;
    border: none;
    margin-bottom: 10px;

    .up {
      transform: rotate(180deg);
    }
  }
  .open {
    display: block;
  }
}
@media (max-width: 450px) {
  .drop-down-parent {
    display: grid;
    grid-template-columns: 120px 120px;
    grid-gap: 6px;
    .drop-down-container {
      left: 60px;
    }
  }
  .search-input {
    .lupa {
      height: 18px;
    }
  }
}
